.ui.image.logo {
    max-width: .9em;
    display: inline-block;
}

.form-error {
    color: #e46d6d;
}

.header-bar {
    background-color: #4eb6ae;
    padding-top: .8em;
    padding-bottom: .6em;
    padding-left: 3em;
    color: #ffffff;
    margin-bottom: 50px;
}

.logout-btn {
    position: absolute;
    right: 1.75em;
    top: 1.2em;
}

.ui.teal.button, .ui.teal.buttons .button {
    margin-top: 1em;
}

.dashboard-username, .dashboard-name {
    text-align: center;
}

footer {
    padding: 1.5em;
    margin-top: 3em;
    width: 100%;
    background-color: rgba(0,0,0,.87);
    color: #fff;
    bottom: 0;
    text-align: center;
}

.github-link, .linkedin-link {
    width: 1.5em;
    color: #fff;
    margin-left: 10px
}

.home, .dashboard {
    min-height: 500px;
}


